import SearchBox from '@/components/common/SearchBox';
// 提示信息
import { showErrorNotify } from '@/utils/notify';
export default {
  name: 'MaintainceTask',
  components: {
    ZTree,
    HeaderTitle,
    HeadOperationItem,
    SearchBox,
    VXETable
  },
  inject: ['reload'],
  data() {
    return {
      VARIABLE: VARIABLE,
      MAINTAINTASK_STATUS: MAINTAINTASK_STATUS,
      EQUIPMENT_DATA: EQUIPMENT_DATA,
      BTN_TEXT: BTN_TEXT,
      PAGE_TITLE: PAGE_TITLE,
      ENERGY: ENERGY,
      TITLE: TITLE,
      MSG: MSG,
      // 弹窗提示
      warmInfo: '',
      nodeData: [],
      // 查询字段
      // 加载表数据标识
      tableData: [],
      tableLoading: false,
      isCanUpload: true,
      uploadConfig: {
        headers: {
          token: window.localStorage.getItem('token').replace(/"/g, '')
        },
        url: IP.PUBLICIP + 'fileResource/uploadOneFile.action',
        data: { // 维修文件
          fileResourceTypeEnum: 'DEVICE_FILE',
          relationshipId: ''
        },
        dataImg: { // 维修图片
          fileResourceTypeEnum: 'DEVICE_FILE',
          relationshipId: ''
        }
      }
    };
  },
  methods: {
    /**
     * @description: 获取树结构
     * @return: void
     */
    getTree() {
      getAllGroupAndDevice().then(res => {
        if (res.code === STATUS_CODE.OPERATION_SUCCESS) {
          this.nodeData = res.rows;
          for (var i = 0; i < this.nodeData.length; i++) {
            if (this.nodeData[i].code) {
              this.selectData = this.nodeData[i];
              break;
            }
          }
        } else {
          showWarningMessage(res.message);
        }
      }).catch(err => {
        showErrorNotify(err);
      });
    },
    /**
     * @description: 根据url下载文件
     * @param: {String} url 文件url
     * @return: void
     */
    async downloadFile(row) {
      downloadFile({
        fileUrl: row.fileUrl
      })
        .then(res => {
          // var arr = url.split('/');
          var filename = row.fileName;
          // var blob = new Blob([res]);
          // var downloadEle = document.createElement('a');
          // var href = window.URL.createObjectURL(blob);
          // downloadEle.href = href;
          // downloadEle.download = filename;
          // downloadEle.click();
          // document.body.removeChild(downloadEle);
          this.$options.filters.setExportFunc(res, filename);
        });
    },
    /**
     * @description: 删除文件
     * @param: {Int} id 文件id
     * @return: void
     */
    deleteFileById(id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      }).then(() => {
        deleteFileById({
          id: id
        })
          .then(res => {
            this.tableData = [];
            this.$message({
              type: 'success',
              message: '删除成功!'
            });
            this.getFilesData();
          });
      }).catch(() => {
        this.$message({
          type: 'info',
          message: '已取消删除'
        });
      });
    },
    /**
     * @description: 树点击事件
     * @param {object} data 树结构渲染数据
     * @param {object} treeNode 树结构真实数据
     * @return: void
     */
    handleNodeClick(data, treeNode) {
      console.log(treeNode);
      if (treeNode.code) {
        this.treeSelectCode = treeNode.code;
        this.treeSelectGroup = '';
        this.uploadConfig.data.relationshipId = Number(treeNode.code.split('_')[1]);
        this.isCanUpload = false;
        // this.$refs.headButtons.showBtn = ['2', '3', '4', '5', '6', '7'];
        // this.selectName = treeNode.code;
        // this.selectCode = treeNode.code;
        // this.showGroup = true;
        // this.toolListTable.page.currentPage = 1;
        // this.toolListTable.page.pageSize = 10;
        // this.getToolListData(treeNode.code);
      } else {
        this.treeSelectCode = '';
        this.treeSelectGroup = treeNode.id;
        this.uploadConfig.data.relationshipId = '';
        this.isCanUpload = true;
        // console.log(treeNode);
        showWarningMessage('请选择设备！');

        // this.treeSelectCode =
        // this.$refs.headButtons.showBtn = ['1', '2', '3', '4', '5', '6', '7', '8'];
        // showWarningMessage(DEVICE_CHECK.PLEASE_CHOOSE_DEVICE);
      }
      this.getFilesData();
    },
    /**
     * @description: 上传文件
     * @return: void
     */
    updateFiles: function() {
      // for (var i = 0; i < this.$refs.upload.uploadFiles.length; i++) {
      //   var isExit = false;
      //   for (var j = 0; j < this.tableData.length; j++) {
      //     if (this.tableData[j].uid === this.$refs.upload.uploadFiles[i].uid) {
      //       isExit = true;
      //     }
      //   }
      //   if (!isExit) {
      //     this.tableData.push(this.$refs.upload.uploadFiles[i]);
      //   }
      // }
    },
    /**
     * @description: 处理上传成功事件
     * @param: {Object} response 上传成功后的返回数据
     * @param: {Object} flie 上传的文件
     * @param: {Array} fileList 文件列表
     * @return: void
     */
    handleUploadSuccess(response, flie, fileList) {
      if (response.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
        this.$message({
          type: 'success',
          message: '上传成功!'
        });
        this.getFilesData();
        this.$refs.upload.uploadFiles = [];
        // for (let index = 0; index < this.tableData.length; index++) {
        //   if (this.tableData[index].uid === flie.uid) {
        //     this.tableData[index].fileUrl = response.rows.fileUrl;
        //     this.tableData[index].resourceId = response.rows.resourceId;
        //   }
        // }
      } else {
        showWarningMessage(response.message);
      }
    },
    getFilesData() {
      selectFile({
        fileResourceTypeEnum: 'DEVICE_FILE',
        relationshipId: this.uploadConfig.data.relationshipId
      })
        .then(res => {
          if (res.code === this.GLOBAL.STATUS_CODE.OPERATION_SUCCESS) {
            this.tableData = res.rows;
          }
        });
    }
  },
  beforeCreate() {
  },
  computed: {
  },
  mounted() {
  },
  created() {
    this.getTree();
  }
};
